@mixin on390 {
  @media (min-width: 390px) {
    @content;
  }
}

@mixin on414 {
  @media (min-width: 414px) {
    @content;
  }
}

@mixin on786 {
  @media (min-width: 786px) {
    @content;
  }
}

@mixin on1024 {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin on1280 {
  @media (min-width: 1280px) {
    @content;
  }
}

.test {
  &__map {
    display: flex;
    justify-content: center;
    padding-top: 8px; //
  }

  &__info {
    padding: 8px 12px; //
    font-size: 9px; //
    font-weight: 300;
    line-height: 140%;
    color: #1C1A59;

    @include on390 {
      padding: 8px 20px;
      font-size: 10px;
    }
    @include on414 {
      padding: 12px 30px;
      font-size: 11px;
    }
    @include on786 {
      padding: 12px 40px;
      font-size: 16px;
    }
    @include on1024 {
      padding: 12px 50px;
      font-size: 20px;
    }
    @include on1280 {
      padding: 12px 70px;
      font-size: 24px;
    }
  }
}

.info {
  display: flex;
  flex-direction: column;

  &--state {
    &::before {
      content: "State: ";
      font-weight: bold;
    }
  }

  &--capital {
    &::before {
      content: "Capital of state: ";
      font-weight: bold;
    }
  }

  &--fun {
    &::before {
      content: "Fun fact: ";
      font-weight: bold;
    }
  }
}